
.Buttom {
  width: 100%;
  height: 56px;
  background-color: #1F2D3D;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  bottom: 0;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.buttom-btn {
  width: 33%;
  padding: 6px 5px 5px 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  color: #fff;
  font-size: 12px;
}
.buttom-btn i {
  color: #fff;
  display: block;
  font-size: 30px;
}



