
.my-header {
  width: 100%;
  height: 56px;
  background-color: #41b883;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.my-header-img {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.my-header-img img {
  width: 100%;
  height: 100%;
}
.my-header-btn button {
  display: block;
  background-color: transparent;
  border: none;
  color: #fff;
}



